import { isYamlKeyValuePair, isYamlMapping } from 'apidom-ast';
import { pathSatisfies, startsWith, both, curry } from 'ramda'; // @ts-ignore

import { hasKeys } from 'apidom-parser-adapter-yaml-1-2'; // isOpenApiExtension :: Options -> YamlKeyValuePair -> Boolean

export var isOpenApiExtension = curry(function (options, node) {
  return both(isYamlKeyValuePair, pathSatisfies(startsWith('x-'), ['key', 'content']))(node);
}); // isServerObject :: Options -> YamlMapping -> Boolean

export var isServerObject = curry(function (options, node) {
  if (!isYamlMapping(node)) {
    return false;
  }

  return hasKeys(['url'], node.content);
}); // isParameterObject :: Options -> YamlMapping -> Boolean

export var isParameterObject = curry(function (options, node) {
  if (!isYamlMapping(node)) {
    return false;
  }

  return hasKeys(['name', 'in'], node.content);
}); // isReferenceObject :: Options -> YamlMapping -> Boolean

export var isReferenceObject = curry(function (options, node) {
  if (!isYamlMapping(node)) {
    return false;
  }

  return hasKeys(['$ref'], node.content);
}); // isRequestBodyObject :: Options -> YamlMapping -> Boolean

export var isRequestBodyObject = curry(function (options, node) {
  if (!isYamlMapping(node)) {
    return false;
  }

  return hasKeys(['content'], node.content);
}); // isResponseObject :: Options -> YamlMapping -> Boolean

export var isResponseObject = curry(function (options, node) {
  if (!isYamlMapping(node)) {
    return false;
  }

  return hasKeys(['description'], node.content);
});